/*//////////////////////////////////////////////////////////////////
[ Шапка ]*/
/* nav {
  width: 90%;
} */
.menu {
    display: flex;
    margin: 0;
   /* padding: 0 0 100px; */
    position: relative;
    font-family: 'Open Sans', sans-serif;
  }
.menu li {
    background-color: #1b1a1a;
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    flex-basis: auto;
    flex-grow: 1;
  }
.subMenu {
  width: 100%;
}  
.menu > li > a {
    padding: 23px 21px;
    display: block;
    color: white;
    font-size: 13px;
    text-decoration: none;
    text-transform: uppercase;
    text-shadow: 0 -1px 0 #0d0d0d;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.7);
    line-height: 18px;
  }
.menu > li:hover > a {
    background-color: red;
  }
.menu > li > a > span {
    line-height: 18px;
  }
  /* Childs */
.menu ul {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 120px;
    background-color: black;
    margin: 0;
    padding: 0;
    z-index: -1;
  }
.menu li:hover ul {
    opacity: 1;
    visibility: visible;
    margin: 0;
    color: #000;
    z-index: 2;
    top: 64px;
    left: 0;
  }
.menu ul:before {
    content: "";
    position: absolute;
    top: -10px;
    width: 100%;
    height: 20px;
    background: transparent;
  }
.menu ul li {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }
.menu ul li a {
    padding: 18px 26px;
    display: block;
    color: white;
    font-size: 13px;
    text-decoration: none;
    /* text-transform: uppercase; */
    border-left: 4px solid transparent;
    -webkit-transition: all 0.20s ease-in-out;
    -moz-transition: all 0.20s ease-in-out;
    -ms-transition: all 0.20s ease-in-out;
    transition: all 0.20s ease-in-out;
  }
.menu ul li a:hover {
    border-left: 4px solid #d64e34;
  }

/*==================================================================
[ Меню языка ]*/
.langs {
  display:none;
}
.langs ul{
  opacity: 1;
  visibility: visible;
  top: 100%;
  z-index: 1;
}
.langs ul input {
  border: none;
  background-color: #1b1a1a;
  color: white;
  font-family: 'Open Sans', sans-serif;
  padding: 18px 26px;
  display: block;
  font-size: 13px;
  text-decoration: none;
  width: 150px;
}
.langs ul input:hover {
  border-left: 5px solid #d64e34;
}
.buttonLangs {
  background-image: url(../img/icons/lang.svg);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  width: 64px;
  height: 64px;
  padding: 23px 26px;
  display: block;
  color: white;
  line-height: 18px;
  text-align: center;
  cursor: pointer;
}
.buttonLangs:hover {
background-color: rgba(0,0,0,.7);
}
.show-block {
  display: block;
  opacity: 1;
}
/*==================================================================
[ Адаптация ]*/ 
.compactLangs,
#checkbox1,
.toggle {
  display: none;
}
@media screen and (max-width: 1062px) {
  .menu li a {
    font-size: 13px;
  }
  #title {
    padding: 50px 0 0 0;
  }
}
@media screen and (max-width: 754px) {
  #title {
    padding: 0;
  }
  .menu li ul {
    display: none;
  }
  .menu > li > a{
      padding: 0 0 0 0;
  }
  .toggle {
    clear: both;
    display: block;
    text-align: center;
    font-size: 14px;
    line-height: 40px;
    cursor: pointer;
    width: 100%;
    height: 40px;
    background: #7e7e7e;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    transition: all .1s linear;
    background-image: url(../img/icons/menuMobile.svg);
    background-size:20px;
    background-repeat:no-repeat;
    background-position:center;
  }
  .compactLangs{
    /* margin-right:4%; */
    width: 50%;
    height: 16%;
    color:white;
    background-color:#1b1a1a;
    text-align: center;
  }
  #checkbox1:checked + label .demo li {
    opacity: 1;
    visibility: visible;
    transition: all .7s linear;
  }
  #checkbox1:checked + label .demo {
    height: 200px;
  }
  #checkbox1:checked + label .compactLangs {
    display: block;
  }
  .menu {
    background-color:#1b1a1a;
    border-radius: 0px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 0px;
    transition: height .3s linear;
    padding: 0;
  }
  .menu li {
    display: flex;
    /* magic */
    align-self: center;
    width: 95%;
    opacity: 0;
    visibility: hidden;
  }
  .menu li a {
    width: 95%;
    text-align: center;
    align-self: center;
    align-content: center;
  }
  li.buttonLangs {
    display: none;
  }
}
